html {
  --black: #000000;
  --gray: #dddddd;
  --white: #f4ecdd;
}

body {
  background-color: var(--white);
  font-family: arial, sans-serif;
  margin: 0;
}

a {
  border-bottom: solid 1px currentcolor;
  color: blue;
  text-decoration: none;

  &:hover {
    border-bottom: none;
  }

  &:visited {
    color: blue;
  }
}

img {
  max-width: 100%;
}
